import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import Palette from './Palette';
import PaletteList from './PaletteList';
import SingleColorPalette from './SingleColorPalette';
import Page from './Page';

import seedColors from './seedColors';
import NewPaletteForm from './NewPaletteForm';
import { generatePalette } from './colorHelpers';





class App extends Component {
  constructor(props) {
    super(props);
    const savedPalettes = JSON.parse(window.localStorage.getItem('palettes'))
    this.state = {
      palettes: savedPalettes || seedColors
    };
    this.savePalette = this.savePalette.bind(this);
    this.findPalette = this.findPalette.bind(this);
    this.deletePalette = this.deletePalette.bind(this);
  }
  findPalette = (id) => this.state.palettes.find(palette => palette.id === id);
  deletePalette(id) {
    this.setState(st =>
      ({ palettes: st.palettes.filter(palette => palette.id !== id) }),
      this.syncLocalStorage
    )
  }
  savePalette(newPalette) {
    this.setState({ palettes: [...this.state.palettes, newPalette] }, this.syncLocalStorage)
  }
  syncLocalStorage() {
    const palettes = JSON.stringify(this.state.palettes)
    window.localStorage.setItem('palettes', palettes)
  }
  render() {
    return (
      <Route render={({ location }) =>
        <TransitionGroup>
          <CSSTransition key={location.key} classNames='page' timeout={500}>
            <Switch location={location}>
              <Route
                exact
                path='/palette/new'
                render={(routeProps) =>
                  <Page>
                    <NewPaletteForm savePalette={this.savePalette} {...routeProps} palettes={this.state.palettes} />
                  </Page>
                }
              />
              <Route
                exact
                path='/palette/:paletteId/:colorId'
                render={(routeProps) =>
                  <Page>
                    <SingleColorPalette
                      palette={generatePalette(this.findPalette(routeProps.match.params.paletteId))}
                      colorId={routeProps.match.params.colorId}
                    />
                  </Page>
                }
              />flexwrap
              <Route
                exact
                path='/'
                render={(routeProps) =>
                  <Page>
                    <PaletteList palettes={this.state.palettes} deletePalette={this.deletePalette} {...routeProps} />
                  </Page>
                }
              />
              <Route
                exact
                path='/palette/:id'
                render={(routeProps) =>
                  <Page>
                    <Palette palette={generatePalette(this.findPalette(routeProps.match.params.id))} />
                  </Page>
                }
              />
             <Route
                render={(routeProps) =>
                  <Page>
                    <PaletteList palettes={this.state.palettes} deletePalette={this.deletePalette} {...routeProps} />
                  </Page>
                }
              />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
      }
      />
    )
  }
}

export default App;
